$primary-color: #b1e693;
$secondary-color: #6ecb63;
$tertiary-color: #270082;

// laptop navbar
// main navbar
.navbar-section {
    display: flex;
    justify-content: space-around;
    position: fixed;
    width: 100%;
    top: 0;
    transition: 0.5s ease;
    z-index: 200;

    .logo {
        height: 90px;
        padding: 20px;
    }

    .navbar-main {
        .navbar-ul {
            list-style: none;
            display: flex;

            .navbar-li {
                padding: 30px;
                font-weight: 200;
                color: #3e8036;
                transition: ease 0.4s;

                &:hover {
                    cursor: pointer;
                    color: $tertiary-color;
                }
            }
        }
    }
}
//  buttons
@mixin button($margin: 0) {
    margin: $margin;
    padding: 10px 15px;
    border: none;
    background-color: $secondary-color;
    border-radius: 10px;
    font-weight: 200;
    font-size: 15px;
    transition: linear 0.5s;
    box-shadow: inset 0 0 0 0 white;
    color: #fcfcfc;

    &:hover {
        box-shadow: inset 400px 0 0 0 white;
        cursor: pointer;
        color: $tertiary-color;
    }
}
.contact-button {
    @include button(17px);
}
.resume-button {
    @include button(17px 10px 17px 100px);
}
.resume-button_about {
    @include button(0 0 0 0);
}

// remove display for mobile
@media (max-width: 900px) {
    .navbar-section {
        display: none;
    }
}
