
$primary-color: #B1E693;
$secondary-color:#6ECB63;
$tertiary-color:#270082;

// main div
@keyframes swing-in-top-fwd {
    0% {
      transform: rotateX(-100deg);
      transform-origin: top;
      opacity: 0;
    }
    100% {
      transform: rotateX(0deg);
      transform-origin: top;
      opacity: 1;
    }
  }


.about-section {
    display: flex;
    visibility: hidden;
    background-color: white;
    margin: 200px;
    margin-bottom: 20px;
    padding: 70px;
    border: none;
    border-radius: 15px;


}
// details
.about-details {

    .about-title {
     color: $tertiary-color;
     margin-bottom: 20px;
     font-size: 32px;
    }
    p {
        line-height: 1.5;
        margin-bottom: 20px;
        font-size: 17px;
    }
}
// pfp
.shahnawaz {
    margin-left: 100px;
    width: 100%;
    height: 100%;
    border-radius: 15px;
}

@media (max-width:1200px) {
    .about-section {
        flex-direction: column;
    }
    .shahnawaz {
        display: none;
    }
}
@media (max-width:900px) {
    .about-section {
        margin: 300px 50px 0 50px;
        padding: 50px;

    }
}
@media (max-width:500px) {
    .about-section {
        margin: 200px 15px 0 15px;
        padding: 35px;
    }
}
