
$primary-color: #B1E693;
$secondary-color:#6ECB63;
$tertiary-color:#270082;


.skills-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 12% 0;
    transition: 0.4s ease;
    scroll-behavior: smooth;
    margin-bottom: 0;

    
    .skill-mobile {
        display: none;
    }
}

// small tablet 

@media (max-width:900px) {

    .skills-section {
        display: block;

        .skill-desktop {
            display: none;
        }
        .skill-mobile {
            display: block;
            margin: 0 50px;
        }
    }
   
}

// svg animations 
#react-hex,
#html-hex,
#javascript-hex,
#redux-hex,
#nodejs-hex,
#mongo-hex,
#css-hex,
#sass-hex,
#mysql-hex,
#figma-hex,
#bootstrap-hex,
#adode-hex,
#jquery-hex,
#git-hex,
#github-hex,
#express-hex,
#vscode-hex  {
    transition: 0.4s ease;
    transform-origin: center;
     transform-box: fill-box;
    
    &:hover {
     transform: scale(0.8);
     
    }
}
@keyframes scale-in-center {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
    #react-hex {
    animation: scale-in-center 0.5s both;
  }
  #html-hex {
    animation: scale-in-center 0.5s 0.3s both;
  }
  #javascript-hex {
    animation: scale-in-center 0.5s 0.5s both;
  }
  #redux-hex {
    animation: scale-in-center 0.5s 0.7s both;
  }
  #nodejs-hex {
    animation: scale-in-center 0.5s 0.2s both;
  }
  #mongo-hex {
    animation: scale-in-center 0.5s 0.4s both;
  }
  #css-hex{
    animation: scale-in-center 0.5s 0.9s both;
  }
  #sass-hex {
    animation: scale-in-center 0.5s 1s both;
  }
  #mysql-hex {
    animation: scale-in-center 0.5s 0.8s both;
  }
  #figma-hex {
    animation: scale-in-center 0.5s 1.2s both;
  }
  #bootstrap-hex {
    animation: scale-in-center 0.5s 1.1s both;
  }
  #adode-hex {
    animation: scale-in-center 0.5s 0.1s both;
  }
  #jquery-hex {
    animation: scale-in-center 0.5s 1.5s both;
  }
  #git-hex {
    animation: scale-in-center 0.5s 1.3s both;
  }
  #github-hex {
    animation: scale-in-center 0.5s 0.6s both;
  }
  #express-hex {
    animation: scale-in-center 0.5s 0.5s both;
  }
  #vscode-hex {
    animation: scale-in-center 0.5s 0.5s both;
  }
  


