$primary-color: #b1e693;
$secondary-color: #6ecb63;
$tertiary-color: #270082;

// main div
.projects-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 12%;
}
// title
.projects-title {
    color: $tertiary-color;
    font-size: 32px;
    margin-bottom: 20px;
}
// each div
.project-div {
    margin: 30px;
    border: none;
    border-radius: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
// hover details
.project-hover {
    opacity: 0;
    padding: 150px 100px;
    border-radius: 20px;
    transition: 0.7s ease;
    display: flex;
    flex-direction: column;
    align-items: center;

    &:hover {
        background: rgba(5, 5, 5, 0.842);
    }
    .project-description {
        font-size: 20px;
        color: white;
        line-height: 1.5;
        text-align: center;
        margin: 0px 100px;
    }
    .project-button {
        padding: 10px 15px;
        border: none;
        border-radius: 20px;
        background-color: $tertiary-color;
        color: #c7f7c2;
        margin: 20px;
        font-size: 15px;
        transition: 0.4s ease;

        &:hover {
            cursor: pointer;
            background-color: white;
            color: #3b6f35;
        }
    }
    .project-icons {
        margin: 20px 5px 0 5px;
    }
}
.project-div:hover > .project-hover {
    opacity: 1;
}

.day-60 {
    background-image: url("../images/60dayD.png");
}
.board {
    background-image: url("../images/boardFolio.png");
}
.c-emb {
    background-image: url("../images/cemb.png");
}
.post-it {
    background-image: url("../images/postit.png");
}

@media (max-width: 900px) {
    .day-60 {
        background-image: url("../images/60dayM.png");
    }
    .project-hover {
        padding: 10px 20px;

        .project-description {
            margin: 10px 10px;
            text-align: start;
            font-size: 17px;
        }
    }
}
