
$primary-color: #B1E693;
$secondary-color:#6ECB63;
$tertiary-color:#270082;


// main div 
.landingpage {
    display: flex;
    justify-content: space-around;
    padding-top: 100px;
}
// titles

  @keyframes slide-in-blurred-top {
    0% {
      transform: translateY(-1000px) scaleY(2.5) scaleX(0.2);
      transform-origin: 50% 0%;
      filter: blur(40px);
      opacity: 0;
    }
    100% {
      transform: translateY(0) scaleY(1) scaleX(1);
      transform-origin: 50% 50%;
      filter: blur(0);
      opacity: 1;
    }
  }
  
.title-1 {
    margin-top: 50px ;
    color: $tertiary-color;
    font-weight: 700;
    font-size:32px ;
    animation: slide-in-blurred-top 0.4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}
.title-2 {
    display: flex;
    flex-direction: column;

    .name {
        color: white;
        font-size: 80px;
        margin: 10px 0;
        position: relative;
        animation: slide-in-blurred-top 0.4s 0.7s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;


        &::after {
            content: "";
            position: absolute;
            width: 0;
            height: 4px;
            bottom: 0;
            left: 0;
            background-color: $tertiary-color;
            visibility: hidden;
            transition: all 0.7s ease;
        }
        &:hover::after {
            visibility: visible;
            width: 90%;
        }

        
    
    }
    .dev {
        color: $tertiary-color;
        font-size: 40px;
        animation: slide-in-blurred-top 0.4s 1.2s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;

    }
}

// scroll down 
.box{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
    margin-top: 7%;
}

.box span{
	display: block;
	width: 20px;
	height: 20px;
	border-bottom: 2px solid white;
	border-right: 2px solid white;
	transform: rotate(45deg);
	margin: -10px;
	animation: animate 2s infinite;
}

.box span:nth-child(2)
{
	animation-delay: -0.2s;
}

.box span:nth-child(3)
{
	animation-delay: -0.4s;
}

@keyframes animate{
	
	0%{
		opacity: 0;
		transform: rotate(45deg) translate(-20px, -20px);
	}
	50%{
		opacity: 1;
	}
	100%{
		opacity: 0;
		transform: rotate(45deg) translate(20px, 20px);
	}
}

// mobile 
@media (max-width:1200px) {
    .welcome-snippet, .css-snippet {
     display: none;
    }
}
@media (max-width:700px) {
    .landingpage {
        display: block;
        margin: 20px;
    }
    .title-1 {
        font-size: 30px;
    }
    
    .title-2{
        .name {
            font-size: 60px;
            margin: 20px 0 ;
        }
        .dev {
            font-size: 40px;
        }
        
    }
    
}
@media (max-width:500px) {
    .title-1 {
        font-size: 20px;
    }
    .title-2 {
        .name {
            font-size: 40px;
        }
        .dev {
            font-size: 30px;
        }
        
    }
}