$primary-color: #B1E693;
$secondary-color:#6ECB63;
$tertiary-color:#270082;


.menu-wrap {
    position: fixed;
    bottom: 10px;
    right: 25px;
    z-index: 1;

    .toggler {
        position: absolute;
        bottom: 10px;
        right:10px;
        z-index: 2;
        cursor: pointer;
        width: 50px;
        height: 50px;
        opacity: 0;
    }

    .hamburger {
        position: absolute;
        bottom: 10px;
        right:10px;
        z-index: 1;
        width: 60px;
        height: 60px;
        padding: 1rem;
        background-color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50px;
    }
}
// hamburger line 
.menu-wrap {

    .hamburger {
        div {
            position: relative;
            width: 100%;
            height: 2px;
            background-color: $tertiary-color;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.4s ease;
        }
        div:before, div:after {
          content: '';
          position: absolute;
          z-index: 1;
          top: -10px;
          width: 100%;
          height: 2px;
          background: inherit;
        }
        div:after {
            top: 10px;
        }
    }
}

// toggler animate 
.menu-wrap {
    .toggler:checked + 
       .hamburger {
            div {
                transform: rotate(135deg);
            }
            div:after, div:before  {
                top: 0;
                transform: rotate(90deg);
            }
        }

}

// toggle menu 
.toggler:checked ~
  .menu {
      height: 300px;
      transition-duration: 0.4s;

      li {
          transform: scale(1);
      }
  }


// menu 

.menu-wrap {
  .menu {
      position: fixed;
      bottom:100px;
      right: 10px;
      line-height: 1.3;
      height: 0px;
      transition: all 0.5s ease;

      ul {
          list-style: none;
          text-align:center ;

          li {
              transform: scale(0);
              transition: all 0.5s ease;
              background-color: white;
              border-radius: 5px;
              box-shadow: 3px 3px 3px $tertiary-color;
              padding: 13px;
              margin: 13px;
              color: $tertiary-color;
              font-weight: 200;
              font-size: 14px;
              
          }
      }
  }
}
// logo sizing 
.hamburger-logo {
    display: none;
}

@media (min-width:900px) {
    .menu-wrap {
        display: none;
    }
}

        

